.welcomeSplash {
  z-index: 10;
  height: auto;
  width: 30%;
  border-radius: 20px;
  border: 2px solid $primaryColour;
  box-shadow: 2px 2px 4px $bgColour;
  margin: 125px auto 0 auto;
  background: $white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  h1 {
    text-align: center;
    color: $accentColour;
  }
  p {
      color: lighten($black, 30%);
      text-align: center;
      letter-spacing: .2rem;
  }
}
.userLoginHome {
  text-align: center;
  color: $accentColour;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 40px;
}
.guestLoginButton {
  font-family: Roboto, helvetica, arial, sans-serif;
  font-size: 14px;
  position: relative;
  letter-spacing: 0;
  outline: 0;
  cursor: pointer;
  text-decoration: none;
  vertical-align: middle;
  border: none;
  border-radius: 2px;
  color: white;
  margin: 0;
  min-width: 64px;
  display: inline-block;
  background-color: #f4b400;
  font-weight: 500;
  line-height: normal;
  max-width: 185px;
  width: 80%;
  min-height: 40px;
  padding: 13px 16px;
  text-align: left;
  width: 100%;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  .userIcon {
    margin-right: 15px;
  }
}

.logo {
  display: block;
  width: 40%;
  margin: 20px auto;
}

.bounce-in-fwd {
  -webkit-animation: bounce-in-fwd 1.1s both;
  animation: bounce-in-fwd 1.1s both;
}

